@media screen and (min-width: 320px) {
  .div {
    display: flex;
    flex-direction: column;
  }
  .padding {
    padding-top: 56px;
    padding-bottom: 40px;
    padding-left: 22px;
    padding-right: 22px;
  }
  .ul {
    gap: 8px;
  }
  .preH2 {
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.375;
  }
  .h2 {
    margin-bottom: 24px;
    font-size: 32px;
    line-height: 1.375;
  }
  .postH2 {
    margin-bottom: 31px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.375;
  }
}
@media screen and (min-width: 768px) {
  .div {
    flex-direction: row;
    width: 768px;
    margin-left: auto;
    margin-right: auto;
  }
  .padding {
    padding-top: 48px;
    padding-bottom: 40px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .ul {
    grid-template-columns: repeat(3, 1fr);
    gap: 18px;
  }
  .preH2 {
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
  }
  .h2 {
    margin-bottom: 24px;
    font-size: 40px;
    line-height: 1.35;
  }
  .postH2 {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
  }
}

@media screen and (min-width: 1360px) {
  .div {
    width: 1360px;
  }
  .padding {
    padding-top: 80px;
    padding-bottom: 80px;
    padding-left: 28px;
    padding-right: 28px;
  }
  .preH2 {
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.83;
  }
  .h2 {
    margin-bottom: 24px;
    font-size: 40px;
    line-height: 1.2;
  }
  .postH2 {
    margin-bottom: 36px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
  }
  .ul {
    grid-template-columns: repeat(3, 1fr);
    gap: 21px;
  }
}
.modalDiv {
  position: relative;
}
.modalText {
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.3);
  color: var(--primary-text-color);
}
.nextArrowModalDiv {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 100%;
  display: flex;

  align-items: center;
}
.nextArrowModalBtn {
  outline: none;
  border: none;
  background-color: transparent;
  height: 150px;
  transition: var(--main-transition);
}
.nextArrowModalBtn:hover,
.nextArrowModalBtn:focus {
  opacity: 0.5;
}
.prevArrowModalDiv {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  display: flex;
  align-items: center;
}
.prevArrowModalBtn {
  outline: none;
  border: none;
  background-color: transparent;
  height: 150px;
  transition: var(--main-transition);
}
.prevArrowModalBtn:hover,
.prevArrowModalBtn:focus {
  opacity: 0.5;
}
.div {
  display: flex;
  background-color: var(--primary-text-color);
}
.preH2 {
  text-align: center;
  color: var(--secondary-text-colo);
}
.h2 {
  text-align: center;
  color: var(--secondary-text-colo);
}
.postH2 {
  text-align: center;
  color: var(--secondary-text-colo);
}
.ul {
  display: grid;
  cursor: pointer;
}
